import { initialPostData } from "../../data/static/postData";
import { successNoty } from "../../components/common/notification";

// Actions
export const FETCH_POST_INITIAL = "FETCH_POST_INITIAL";
export const FETCH_POST_SUCCESS = "FETCH_POST_SUCCESS";
export const FETCH_POST_FAILED = "FETCH_POST_FAILED";
export const ADD_NEW_POST = "ADD_NEW_POST";
export const ADD_NEW_COMMENT = "ADD_NEW_COMMENT";
export const ADD_NEW_UPVOTE = "ADD_NEW_UPVOTE";
export const SET_HOME_ACTION_ERROR = "SET_HOME_ACTION_ERROR";
export const CLEAR_HOME_ACTION_ERROR = "CLEAR_HOME_ACTION_ERROR";

// Intial State
const initialState = {
  loading: false,
  error: null,
  posts: [],
  actionError: null,
};

// Action Creator

export const fetchPostInitial = () => {
  return {
    type: FETCH_POST_INITIAL,
  };
};

export const fetchPost = () => {
  return (dispatch, getState, { getFirebase }) => {
    const firestore = getFirebase().firestore();
    // firestore.ref("posts").on("value", (snapshot) => {
    //   console.log("FireB ", snapshot);
    //   if (snapshot && snapshot.exists()) {
    //     //Set values in state which can be extracted in jsx in render.
    //   }
    // });
    dispatch(fetchPostInitial);
    //Call
  };
};

export const createNewPost = (post) => {
  return (dispatch, getState, { getFirebase }) => {
    const firestore = getFirebase().firestore();
    firestore
      .collection("posts")
      .add(post)
      .then(() => {
        dispatch({
          type: ADD_NEW_POST,
          payload: post,
        });
      })
      .catch(() => {});
  };
};

export const addNewComment = (id, comment) => {
  return (dispatch, getState, { getFirebase }) => {
    const firestore = getFirebase().firestore();

    firestore
      .collection("posts")
      .doc(id)
      .collection("comments")
      .add(comment)
      .then(() => {
        dispatch({
          type: ADD_NEW_COMMENT,
          payload: {
            id: id,
            comment: comment,
          },
        });
      })
      .catch(() => {});
  };
};

export const addNewUpvote = (id, user) => {
  return (dispatch, getState, { getFirebase }) => {
    const firestore = getFirebase().firestore();
    firestore
      .collection("posts")
      .doc(id)
      .collection("upvotes")
      .add({ upvotedBy: user })
      .then(() => {
        dispatch({
          type: ADD_NEW_UPVOTE,
          payload: {
            id: id,
            user: user,
          },
        });
      })
      .catch(() => {});
  };
};

export const setHomeActionError = (err) => {
  return {
    type: SET_HOME_ACTION_ERROR,
    payload: err,
  };
};

export const clearHomeActionError = () => {
  return {
    type: CLEAR_HOME_ACTION_ERROR,
  };
};

// Reducer
export function homeReducer(state = initialState, action) {
  switch (action.type) {
    case ADD_NEW_POST: {
      successNoty("Post Added ");
      return {
        ...state,
      };
    }

    case ADD_NEW_COMMENT: {
      return {
        ...state,
      };
    }

    case ADD_NEW_UPVOTE: {
      return {
        ...state,
      };
    }

    case SET_HOME_ACTION_ERROR: {
      return {
        ...state,
        actionError: action.payload,
      };
    }

    case CLEAR_HOME_ACTION_ERROR: {
      return {
        ...state,
        actionError: null,
      };
    }

    default:
      return state;
  }
}
